/* App.css */


.direccion {
  margin: 0px;
  font-size: 30px;
}

.direccion2 {
  margin: 0px;
  font-size: 20px;
}

.direccion-container {
  margin-bottom: 20px;
}

.texto-generico {
  margin: 10px;
  font-size: 20px
}

.pedido {
  position: relative; /* Permite posicionar absolutamente el botón de cancelar */
  border: 1px solid black;
  border-radius: 5px;
  text-align: left;
  box-sizing: border-box;
  overflow: auto;
  margin: 10px;
  padding: 10px;
  width: calc(50% - 20px); /* Ajusta el ancho para pantallas pequeñas */
}

.pedido-content {
  padding-top: 40px; /* Espacio para el botón de cancelar */
}

/*================================================================*/
/* LOGIN */

.login-container {
  display: flex;
  justify-content: center;
  margin-top: 20px; /* Ajusta este valor según lo necesites */
  margin-left: 10px;
  margin-right: 10px;
  background-color: gray;
}

.login-form-container {
  padding: 40px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 400px;
}

.login-title {
  margin-bottom: 20px;
  text-align: center;
}

.login-input {
  width: calc(100% - 20px);
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid #ddd;
  box-sizing: border-box;
}

.login-button {
  width: calc(100% - 20px);
  padding: 10px;
  margin: 20px 0;
  border-radius: 5px;
  border: none;
  background-color: #5c6bc0;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

/*================================================================*/
/* MAIN SCREEN */

.home-container {
  text-align: center;
  margin-top: 15px;
}

.controls-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

/*-----------------------------------*/
/* Contador de Moviles */

.contadorMoviles-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.arrow-button {
  border: none;
  background-color: transparent;
  padding: 0; /* Elimina el relleno predeterminado */
  margin: 0; /* Elimina el margen predeterminado */
}

.arrow {
  font-size: 60px;
  line-height: 1; /* Reduce el espacio extra alrededor de las flechas */
}

.contadorMoviles-input {
  width: 50px;
  height: 50px;
  text-align: center;
  font-size: 30px;
  border: 1px solid black;
  margin: 0; /* Elimina el margen */
  line-height: 50px; /* Alinea el texto verticalmente */
}

.nombrePasajero-input {
  width: 350px;
  height: 40px;
  text-align: left;
  font-size: 25px;
  border: 1px solid black;
}

/*-----------------------------------*/
/* Tick Box */

.tick-items-container {
  margin: 20px;
}
/* Estilos generales para los items con casillas de tick */
.tick-items-container .tick-item {
  display: flex;
  align-items: center;
  text-align: left;
  margin-bottom: 10px; /* Espacio entre cada item */
  -webkit-appearance: none; /* Elimina el estilo por defecto en navegadores WebKit */
  -moz-appearance: none; /* Elimina el estilo por defecto en navegadores Firefox */
  appearance: none; /* Elimina el estilo por defecto en otros navegadores */
}

/* Estilizar los checkboxes */
.tick-items-container input[type="checkbox"] {
  width: 2.5em; /* Tamaño del checkbox */
  height: 2.5em; /* Tamaño del checkbox */
  cursor: pointer;
  -webkit-appearance: none; /* Elimina el estilo por defecto en navegadores WebKit */
  -moz-appearance: none; /* Elimina el estilo por defecto en navegadores Firefox */
  appearance: none; /* Elimina el estilo por defecto en otros navegadores */
  background-color: #f6f6f6; /* Color de fondo */
  border: 2px solid #dcdcdc; /* Borde del checkbox */
  border-radius: 5px; /* Bordes redondeados */
  display: inline-block;
  vertical-align: middle;
}

/* Estilo cuando el checkbox está marcado */
.tick-items-container input[type="checkbox"]:checked {
  background-color: #4caf50; /* Color de fondo cuando está marcado */
  border: 1px solid white; /* Borde del checkbox cuando está marcado */
}

/* Estilo para la etiqueta asociada al checkbox */
.tick-items-container label {
  margin-left: 5px; /* Espacio entre el checkbox y la etiqueta */
  cursor: pointer;
  font-size: 18px;
}

/*-----------------------------------*/
/* Boton Pedir */

.pedir-button {
  width: 180px;
  height: 180px;
  margin-top: 25px;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 50%;
  border: none;
  background-color: green;
  color: white;
  font-size: 25px;
  cursor: pointer;
  transition: background-color 0.3s;
}

/*-----------------------------------*/
/* Lista de Moviles */

.pedidos-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}

.movil {
  border: 1px solid black;
  border-radius: 5px;
  text-align: left;
  box-sizing: border-box;
  overflow: auto;
  margin: 10px;
  padding: 10px;
  padding-top: 60px; /* Aumentar el espacio en la parte superior */
  position: relative; /* Necesario para posicionar el botón de cancelar */
  width: calc(100% - 100px);
}


@media (min-width: 600px) {
  .movil {
    width: 350px; /* Ancho fijo para pantallas más grandes */
  }
}

/* Estilos para el número de móvil */
.movil-number {
  position: absolute;
  top: 0px;
  font-size: 1.8em;
  font-weight: bold;
}

.movil-datos {
  font-size: 1.9em;
  margin: 0px;
}

.login-button:hover {
  background-color: #3f51b5;
}

.pedir-button:hover {
  background-color: darkgreen;
}

.cancel-button {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 3em;
  height: 3em;
  border-radius: 50%;
  background-color: red;
  color: white;
  font-size: 15px;
  cursor: pointer;
  border: none;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.status-button {
  position: absolute;
  top: 10px;
  right: 50px;
  width: 3em;
  height: 3em;
  border-radius: 50%;
  background-color: #3f51b5;
  color: white;
  font-size: 15px;
  cursor: pointer;
  border: none;
  align-items: center;
  justify-content: center;
  padding: 0;
}

@media (min-width: 600px) {
  .cancel-button {
    width: 2em;
    height: 2em;
  }
  .status-button {
    width: 2em;
    height: 2em;
  }
}

.cancel-button-text {
  margin: 0; /* Elimina margenes adicionales */
  font-size: 25px;
  color: black;
}

.cancel-button:hover {
  background-color: darkred;
}

/*-------------------------------------*/

.button-volver {
  position:fixed;
  top: 10px;
  left: 10px;
  padding: 0px 10px;
  border-radius: 5px;
  border: none;
  background-color: #4CAF50;
  color: white;
  font-size: 14px;
  cursor: pointer;
  outline: none;
  transition: all 0.3s ease;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
}

.button-volver:hover {
  background-color: #45a049;
}

.button-volver:active {
  transform: scale(0.98);
}

.register-button {
  display: block;
  margin: 10px auto; /* Centra el botón */
  padding: 10px 20px;
  border-radius: 5px;
  border: none;
  background-color: #007bff; /* Color de fondo */
  color: white; /* Color del texto */
  font-size: 16px;
  cursor: pointer;
  outline: none;
  transition: all 0.3s ease;
}

.registro-container {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  border-radius: 8px;
}

.registro-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.registro-form input[type="text"],
.registro-form input[type="password"],
.registro-form input[type="checkbox"] {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ddd;
}

.registro-form label {
  margin-left: 5px;
}

.btn {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.submit-btn {
  background-color: #4CAF50;
  color: white;
}

.back-btn {
  background-color: #f44336;
  color: white;
}



